import { mapState, mapMutations } from 'vuex'; 

export default {
	data() {
		return {
			PageCur: 'cards'
		}
	},
	computed:{
		...mapState(['login', 'userName'])  
	},
	onLoad() {
	
	},
	methods: {
		 ...mapMutations(['logout']) ,
			NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
	}
}
